import React, {useState} from "react"
import Axios from "axios"
import "./Login.css"
import AMSLogo from "./Imgs/AMS.png"

export default function Login(props){
    const [loginCred, setLoginCred] = useState({
        username : "",
        password : ""
    })
    const [status , setStatus]= useState("")

    const login = () => {
        Axios.post("https://ams-server-svu7.onrender.com/login", loginCred)
        .then((response) => {
          response.data.loggedIn ? 
            props.handleLogin(response) 
          : setStatus(response.data.message)
            
        })
    }

    return(
        <div className="Login-form">
            <img src={AMSLogo} className="login--logo"/>
            <h1>WELCOME TO:</h1>
            <h3>Asset Management System(A.M.S.)</h3>

            <label>Username</label>
            <input
            type="text"
            onChange={(e) => {
                setLoginCred(prevValue=>{
                return {
                    ...prevValue,
                    username: e.target.value
                }
                })
            }}
            />

            <label>Password</label>
            <input
            type="text"
            onChange={(e) => {
                setLoginCred(prevValue=>{
                return {
                    ...prevValue,
                    password: e.target.value
                }
                })
            }}
            />
            
            <button onClick={login}> login </button>
            {status}

        </div>
    )
}