import React,{useState} from "react"
import "./ContentPanel.css"
import Dashboard from "./Dashboard"
import ItemStocks from "./ItemStocks"
import ItemType from "./ItemType"
import AssignItems from "./AssignItems"
import AssignSets from "./AssignSets"
import DeviceLocations from "./DeviceLocations"
import AddLocations from "./AddLocations"
import SystemUser from "./SystemUser"
import ActivityLog from "./ActivityLog"
import UserLog from "./UserLog"

export default function ContentPanel(props){
    //const [activePanel , setActivePanel]= useState(props.currentScreen)
    //setActivePanel(props.currentScreen)
    return(
        <div className="ContentPanel">
            {props.currentScreen === "Dashboard" && <Dashboard currentUser={props.currentUser}/>}
            {props.currentScreen === "ItemStocks" && <ItemStocks />}
            {props.currentScreen === "ItemType" && <ItemType />}
            {props.currentScreen === "AssignItems" && <AssignItems />}
            {props.currentScreen === "AssignSets" && <AssignSets />}
            {props.currentScreen === "DeviceLocations" && <DeviceLocations />}
            {props.currentScreen === "AddLocations" && <AddLocations />}
            {props.currentScreen === "SystemUser" && <SystemUser />}
            {props.currentScreen === "ActivityLog" && <ActivityLog />}
            {props.currentScreen === "UserLog" && <UserLog />}
        </div>
    )
}